  position: relative;
}

.flow-sidebar.collapsed {
  width: 0;
  padding: 0;
  border-left: none;
  overflow: hidden;
}

/*  */
.sidebar-toggle {
  position: fixed;
  right: 365px; /*  + 15px */
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: #4285f4;
